.App-board {
    border-radius: 2%;
    margin: auto;
    margin-top: 10vh;
    background-color: #f0f0f0;
    width: 50vh;
    height: 80vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: end;
        align-content: flex-end;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.empty-square {
    border: 2px solid #fff;
    margin: 1%;
    width: 8vh;
    height: 8vh;
    background: white;
}

.App-empty {
    width: 8vh;
    height: 8vh;
    border-radius: 50%;
    margin: 1vh;
    visibility: hidden;
}

.App-box {
    -webkit-box-shadow: 0.5vh 0.5vh 0px #e2e2e2;
            box-shadow: 0.5vh 0.5vh 0px #e2e2e2;
    width: 8vh;
    height: 8vh;
    border-radius: 50%;
    margin: 1vh;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
    -webkit-transition-property: ratio, border-radius;
    -o-transition-property: ratio, border-radius;
    transition-property: ratio, border-radius;
    background-color: #77eedf;
}

.App-box2 {
    background-color: #169ea7;
}

.App-box3 {
    background-color: #086c6d;
}

.App-box4 {
    background-color: #e57066;
}

.App-box:active {
    border-radius: 25%;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    
}



